.Item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
    width: 180px;
    height: 20px;
    border: 1px solid #ede7f6;
    border-radius: 2px;
    padding: 10px;
    background: rgb(51, 20, 1);
}

.Item p {
    margin: 0;
    font-family: cursive;
    font-size: 14px;
}

.Item button {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    padding: 2px;
    outline: none;
}
