.App {
  text-align: center;
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Wrapper{
  background-color: black;
  padding: 10px;
  border-radius: 10%;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.5);
}

.Input-wrapper{
  display: flex;
  justify-content: space-between;
  width: 200px;
  align-items: center;
  
}

.Input-wrapper input{
  border-radius: 4%;
  border: 0;
  background-color: blanchedalmond;
  width: 160px;  
  padding: 6px;
  height: 20px;
  margin: 10px;
}

.Input-wrapper button{
  width: 20px;
  height: 20px;
  border-radius: 50%;
  padding: 2px;
  outline: none;
}
